<template>
  <div class="box">
    <h3 v-show="true">搜索"{{ myinput }}"</h3>
    <!-- {{ changedata }} -->
    <ul>
      <li @click="goto(v.keyword)" v-for="(v, i) in tip" :key="i">
        <i></i><span>{{ v.keyword }}</span>
      </li>
    </ul>
    <Loading v-show="flag" />
  </div>
</template>
<script>
import Loading from "../components/Loading";
export default {
  components: {
    Loading,
  },
  computed: {
    flag() {
      if (this.myinput != "" && this.tip.length <= 0) {
        return true;
      } else {
        return false;
      }
    },
  },
  props: ["tip", "myinput"],
  methods: {
    goto(v) {
      this.$router.push("/search/list/" + v).catch(() => {});
    },
  },
  //   computed: {
  //     data() {
  //       if (this.changedata) {
  //         return this.changedata;
  //       } else {
  //         return [{ name: "" }];
  //       }
  //     },
  //   },
};
</script>
<style lang="less" scoped>
.box {
  width: 100%;
  h3 {
    overflow: hidden;
    font-size: 15px;
    color: rgb(80, 125, 175);
    height: 50px;
    line-height: 50px;
    margin-left: 10px;
    border-bottom: 1px solid rgb(242, 243, 244);
  }
  ul {
    li {
      display: flex;
      height: 45px;
      padding-left: 10px;
      overflow: hidden;
      i {
        width: 15px;
        margin-right: 7px;
        background: url(data:image/svg+xml;base64,)
          no-repeat center;
        background-size: 15px;
      }
      span {
        font-size: 15px;
        color: #333;
        line-height: 45px;
        box-sizing: border-box;
        flex: auto;
        border-bottom: 1px solid rgb(242, 243, 244);
      }
    }
  }
}
</style>